@charset "UTF-8";
:root {
  --font-family-base: "Zen Old Mincho", "Hiragino Mincho ProN", "Yu Mincho", serif;
  --font-family-second: "Inter", "Hiragino Sans", "Yu Gothic", "Noto Sans JP", sans-serif;
  --font-family-third: "Zen Kaku Gothic New", "Hiragino Kaku Gothic ProN", "Yu Gothic", "Noto Sans JP", sans-serif;
  --font-family-fourth: "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Yu Gothic", sans-serif;
}

:root {
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-black: 900;
}

:root {
  --black: #000;
  --white: #fff;
  --text-color: #626262;
  --text-gray: #ACACAC;
  --main-color: #E60012;
  --main-color2: #C4A552;
  --bg-color: #F9F6EE;
}

:root {
  --z-index-header: 100;
  --z-index-to-top: 200;
  --z-index-hamburger: 1000;
  --z-index-float-entry: 1000;
  --z-index-modal: 10000;
  --z-index-drawer: 10000;
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

:root {
  --cq-ref-mobile: 375;
  --cq-ref: 1440;
  /* スマホ〜中型（320px〜375px） */
  --fluid-basis-mobile: (100vw - 320px) / (375 - 320);
  /* 中型〜大型（768px〜1440px） */
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  /* 大型ディスプレイ（1440px〜1800px） */
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

body {
  color: var(--text-color);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* グローバルリセット */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* テキストサイズの調整をリセット */
html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

/* ボディの基本設定 */
body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root:not(:lang(ja)) body {
  overflow-wrap: break-word;
  hyphens: auto;
  word-break: normal;
  line-break: auto;
}

/* デフォルトの余白とパディングを削除 */
ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

/* リストスタイルのリセット */
ul,
ol {
  list-style: none;
}

/* クラスなしのaタグにデフォルトのスタイルを適用 */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* インラインブロック要素 */
a,
span,
button {
  display: inline-block;
}

/* フォントとマージンのリセット */
small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

/* 画像の調整 */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* 記事内の自然な余白を設定 */
article > * + * {
  margin-top: 1em;
}

/* フォーム要素のフォント継承 */
input,
button,
textarea,
select {
  font: inherit;
}

/* フォームのリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

/* チェックボックスとラジオボタンを非表示 */
input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

/* ボタンやラベルにカーソルを設定 */
input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

/* IE用の選択ボックスアイコンを非表示 */
select::-ms-expand {
  display: none;
}

/* aタグのスタイルとトランジション */
a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

/* ホバー時のカーソル設定 */
@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
/* PCでtelリンクを無効化 */
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
/* タップ時のハイライトを透明に設定 */
a {
  -webkit-tap-highlight-color: transparent;
  /* リンクのハイライトを透明に */
}

button {
  -webkit-tap-highlight-color: transparent;
  /* ボタンのハイライトも透明に */
}

.float-entry-btn-sp {
