@use "global" as *;
/* ===============================================
# フェードアニメーション：IntersectionObserver＆GSAP用
=============================================== */

/* フェード */
.js-fade-in {
  opacity: 0;
  transition: opacity var(--duration-long) ease;
  
  &.is-active {
    opacity: 1;
  }
}

/* 上方向にスライド */
.js-fade-up {
  opacity: 0;
  translate: 0 20px;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
  
  &.is-active {
    opacity: 1;
    translate: 0 0;
  }
}

/* 左方向にスライド */
.js-slide-left {
  opacity: 0;
  translate: -20px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
  
  &.is-active {
    opacity: 1;
    translate: 0 0;
  }
}

/* 下方向にスライド */
.js-fade-down {
  opacity: 0;
  translate: 0 -20px;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
  
  &.is-active {
    opacity: 1;
    translate: 0 0;
  }
}

/* 右方向にスライド */
.js-slide-right {
  opacity: 0;
  translate: 10px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
  @include mq('md') {
    translate: 20px 0;
  }
  &.is-active {
    opacity: 1;
    translate: 0 0;
  }
}


//画像拡大 -> 縮小
.js-scaleImg {
  overflow: hidden;
  img {
    scale: 1.1;
    transition: scale 1.2s var(--ease-smooth-bounce), opacity 1.2s var(--ease-smooth-bounce);
    opacity: 0;
  }
  &.is-active img {
    scale: 1;
    opacity: 1;
  }
}

/* クリップパス */

.js-clip-img{
  overflow: hidden;

  & img {
    scale: 1.3;
    transition: scale 1s var(--ease-smooth-bounce), clip-path 1s var(--ease-smooth-bounce);
    clip-path: inset(0 100% 0 0)
  }

  &.is-active img {
    clip-path: inset(0 0 0 0);
    scale: 1;
  }

  @include mq('md') {
    & img {
      transition: scale 1.2s var(--ease-smooth-bounce), clip-path 1.2s var(--ease-smooth-bounce);
    }
  }
}

.js-clip-item {
  overflow: hidden;
    transition: scale .8s var(--ease-smooth-bounce), clip-path .8s var(--ease-smooth-bounce);
    clip-path: inset(0 100% 0 0);
    &.is-active {
      clip-path: inset(0 0 0 0);
      scale: 1;

      & .top-recruit__link {
        scale: 1;
      }
    }

    & .top-recruit__link {
      scale: 1.3;
      transition: scale 1s var(--ease-smooth-bounce);
    }
}



.js-text-animate.is-active {
  animation: leadSweep 2.8s cubic-bezier(.25,.1,.25,1) forwards;
  display: inline-block;
  background:
    linear-gradient(90deg,
      transparent 0%,
      #E60012 20%,
      #E60012 50%,
      transparent 60%
    ) 0 0 / 200% 100% no-repeat,
    linear-gradient(#626262, #626262) 0 0 / 100% 100% no-repeat;

  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
  animation: leadSweep 2.8s cubic-bezier(.25,.1,.25,1) forwards;
}

@keyframes leadSweep {
  0% { background-position: 100% 0, 0 0; }
  100% { background-position: -100% 0, 0 0; }
}


////////////////////
// 遅延時間の指定
////////////////////

:root {
  --delay-1: 0.1s;
  --delay-2: 0.2s;
  --delay-3: 0.3s;
  --delay-4: 0.4s;
  --delay-5: 0.5s;
}

.--delay-1 {
  transition-delay: var(--delay-1);
}

.--delay-2 {
  transition-delay: var(--delay-2);
}

.--delay-3 {
  transition-delay: var(--delay-3);
}

.--delay-4 {
  transition-delay: var(--delay-4);
}

.--delay-5 {
  transition-delay: var(--delay-5);
}

@media (max-width: 768px) {

  .--delay-1,
  .--delay-2,
  .--delay-3,
  .--delay-4 {
    transition-delay: .0s;
  }
}